/* Global Styles */
@import url('https://fonts.googleapis.com/css?family=Fredoka+One');

:root {
	--primary-color: #17a2b8;
	--dark-color: #343a40;
	--light-color: #f4f4f4;
	--danger-color: #dc3545;
	--success-color: #28a745;
}

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

body {
	font-family: 'Fredoka One', sans-serif;
	font-size: 1rem;
	line-height: 1.6;
	background-color: #fff;
	color: #333;
}

.main,
.craft {
	background-color: black;
	width: 100vw;
	height: 100vh;
}

.background {
	display: flex;
	width: 100%;
	height: 100%;
	align-items: center;
	justify-content: center;
}

.main .background img {
	width: 100%;
	height: auto;
}

.craft .background img {
	width: auto;
	height: auto;
	max-width: 100%;
	max-height: 100%;
}

.absolute {
	position: absolute;
}

.z-10 {
	z-index: 10;
}

.w-full {
	width: 100%;
}

.pos-center {
	top: 50%;
	left: 50%;
	--tw-translate-x: 0;
	--tw-translate-y: 0;
	--tw-translate-z: 0;
	--tw-rotate: 0;
	--tw-rotate-x: 0;
	--tw-rotate-y: 0;
	--tw-rotate-z: 0;
	--tw-skew-x: 0;
	--tw-skew-y: 0;
	--tw-scale-x: 1;
	--tw-scale-y: 1;
	--tw-scale-z: 1;
	-webkit-transform: translateX(var(--tw-translate-x)) translateY(var(--tw-translate-y)) translateZ(var(--tw-translate-z)) rotate(var(--tw-rotate)) rotateX(var(--tw-rotate-x)) rotateY(var(--tw-rotate-y)) rotateZ(var(--tw-rotate-z)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y)) scaleX(var(--tw-scale-x)) scaleY(var(--tw-scale-y)) scaleZ(var(--tw-scale-z));
	-ms-transform: translateX(var(--tw-translate-x)) translateY(var(--tw-translate-y)) translateZ(var(--tw-translate-z)) rotate(var(--tw-rotate)) rotateX(var(--tw-rotate-x)) rotateY(var(--tw-rotate-y)) rotateZ(var(--tw-rotate-z)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y)) scaleX(var(--tw-scale-x)) scaleY(var(--tw-scale-y)) scaleZ(var(--tw-scale-z));
	transform: translate(var(--tw-translate-x)) translateY(var(--tw-translate-y)) translateZ(var(--tw-translate-z)) rotate(var(--tw-rotate)) rotateX(var(--tw-rotate-x)) rotateY(var(--tw-rotate-y)) rotate(var(--tw-rotate-z)) skew(var(--tw-skew-x)) skewY(var(--tw-skew-y)) scaleX(var(--tw-scale-x)) scaleY(var(--tw-scale-y)) scaleZ(var(--tw-scale-z));
	--tw-translate-x: -50%;
	--tw-translate-y: -50%;
}

svg {
	display: block;
	vertical-align: middle;
}

.hoverDlg {
	position: absolute;
}

.hoverDlg .dialog {
	width: 440px;
	height: auto;
}

.hoverDlg .polygon {
	position: absolute;
	bottom: -35px;
	right: 20px;
}

.cursor-pointer {
	cursor: pointer;
}

.craft .background .mask {
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.55);
	backdrop-filter: blur(2.5px);
	position: absolute;
}

.craft .main-content {
	position: absolute;
	top: 0px;
	width: 100%;
	height: 100%;
	padding: 50px;
}

.craft .main-content .header {
	display: flex;
	justify-content: space-between;
}

.button {
	display: flex;
	padding-left: 35px;
	padding-right: 35px;
	height: 70px;
	background-color: white;
	border-radius: 10px;
	font-style: normal;
	font-weight: 400;
	font-size: 33px;
	line-height: 30px;
	text-transform: uppercase;
	align-items: center;
	justify-content: center;
}

.button img {
	margin-right: 22.5px;
}

.craft .content {
	margin-top: 100px;
	margin-bottom: 200px;
	height: calc(100% - 300px);
	display: flex;
	justify-content: center;
}

.craft .content .NFTList:first-child {
	margin-right: 40px;
}

.NFTList {
	background: rgba(255, 255, 255, 0.2);
	border: 4px solid #FFFFFF;
	backdrop-filter: blur(10px);
	width: 500px;
	border-radius: 25px;
	padding: 20px 15px;
	display: flex;
	flex-wrap: wrap;
	overflow-y: scroll ;
}

.NFTList::-webkit-scrollbar {
	display: none;
  }
  
  /* Hide scrollbar for IE, Edge and Firefox */
  .NFTList {
	-ms-overflow-style: none;  /* IE and Edge */
	scrollbar-width: none;  /* Firefox */
  }

.craftbtn {
	background: #FF5A32;
	color: white;
	width: 300px;
	position: absolute;
	bottom: 55px;
	left: calc(50% - 150px);
}

.NFTItem {
	background: black;
	width: 215px;
	border-radius: 20px;
	margin-bottom: 20px;
}

.NFTItem:nth-child(odd) {
	margin-right: 20px;
}

.NFTItem .title {
	font-weight: 400;
	font-size: 23px;
	line-height: 30px;
	/* identical to box height, or 120% */

	text-align: center;
	text-transform: uppercase;
	margin-top: 12px;
	color: #FFFFFF;
}

.NFTItem img {
	border-radius: 20px 20px 0px 0px;
	width: calc(100% - 20px);
	margin-top: 8px;
	margin-left: 10px;
	margin-right: 10px;
}

.NFTItem .selectbtn {
	background: #FFFFFF;
	border-radius: 0px 0px 20px 20px;
	color: black;
	height: 50px;
	font-weight: 400;
	font-size: 25px;
	line-height: 30px;
	/* identical to box height, or 120% */
	margin-top: -7px;
	text-align: center;
	text-transform: uppercase;
	display: flex;
	align-items: center;
	justify-content: center;
}

.NFTItem .selectbtn.selected {
	color: white;
	background: #FF5A32;
}

.button.disabled {
	background: #FFFFFF;
	color: #D4D6E4;
	cursor: no-drop;
}

.content .videoTag {
	
	border: 5px solid #FFFFFF;
	border-radius: 25px;
	position: relative;
	width: 1250px;
	display: flex;
    align-items: center;
    justify-content: center;
	overflow: hidden;
}

.content .videoTag img.backgroundimg{
	border-radius: 25px;
	position: absolute;
	top: 0px;
	width: 100%;
}

.content .videoTag .mask {
	background: rgba(0, 0, 0, 0.45);
	backdrop-filter: blur(3px);
	/* Note: backdrop-filter has minimal browser support */
	width: 100%;
	height: 100%;
	top: 0px;
	border-radius: 25px;
	position: absolute;
}

